/*
 START GLOBAL STYLES
 */
[v-cloak] {
  display: none;
}
html {
  background-color: #eee5e9;
}
body {
  margin: 0;
  background-color: #eee5e9;
  font-family: "Work Sans", sans-serif;
  font-weight: 300;
}
#router-container {
  min-height: calc(100vh - (105px + 40px));
}
.container {
  margin: 5px 15px;
}
@media (max-width: 600px) {
.container {
      margin: 5px 10px;
}
}
.container .content-block {
    border: 2px solid #f0f3f5;
    margin-bottom: 15px;
    padding: 10px 20px;
    background-color: #FFFDF5;
}
.centered {
  text-align: center;
}
.button-wrap {
  display: inline-block;
  width: 100%;
}
.button {
  display: inline-block;
  padding: 10px 20px;
  border-radius: 2px;
  text-shadow: 1px 1px 0 #555;
  background-color: #af7056;
  color: white;
  transition: all 0.5s ease;
  cursor: pointer;
  text-decoration: none;
  text-transform: uppercase;
}
.button:hover {
    background-color: #8f5943;
}
.button.plain {
    background-color: transparent;
    color: #383836;
    text-shadow: none;
    border: 1px solid #000;
}
.button.plain:hover {
      background-color: transparent;
}
.button.big {
    padding: 15px 40px;
    font-size: 1.5em;
}
.button.small {
    padding: 5px 18px;
    font-size: .8em;
}
.button.tiny {
    padding: 3px 14px;
    font-size: .65em;
}
.button.checkout {
    text-align: center;
    margin: 0 0 20px;
}
.button.red {
    border: 1px solid red;
    color: red;
}
.slide-right-enter-active {
  transition: all .3s ease;
}
.slide-right-leave-active {
  transition: all .3s ease;
}
.slide-right-enter, .slide-right-leave-to {
  transform: translateX(50px);
  opacity: 0;
}
#app {
  margin: 0;
  padding: 115px 0 0 0;
  position: relative;
  width: 100%;
}
@media (max-width: 600px) {
#app {
      padding: 100px 0 0 0;
}
}
h1, h2, h3, h4, h5 {
  font-family: "Playfair Display", serif;
  font-weight: 300;
  text-transform: uppercase;
}
h1.no-margin-t, h2.no-margin-t, h3.no-margin-t, h4.no-margin-t, h5.no-margin-t {
    margin-top: 0;
}
h1.no-margin-b, h2.no-margin-b, h3.no-margin-b, h4.no-margin-b, h5.no-margin-b {
    margin-bottom: 0;
}
.title {
  text-transform: uppercase;
  font-size: 1.5em;
  text-decoration: none;
}
.subheader {
  font-size: 1.2em;
  display: block;
  margin: 10px 0;
}
.subtext {
  font-size: .8em;
  display: inline-block;
  color: #7e7e7e;
}
a {
  cursor: pointer;
}
a.no-style {
    text-decoration: none;
    color: inherit;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  height: 105px;
  border: 15px solid #eee5e9;
  box-sizing: border-box;
  /*    box-shadow: 0px 1px 8px 0px $content-text-color;*/
}
@media (max-width: 600px) {
header {
      border: none;
}
}
header .nav-header-text {
    font-family: "Playfair Display", serif;
    font-size: 30px;
}
@media (max-width: 800px) {
header .nav-header-text {
        font-size: 25px;
}
}
@media (max-width: 600px) {
header .nav-header-text {
        display: none;
}
}
header .nav-header-text-mobile {
    font-family: "Playfair Display", serif;
    font-size: 30px;
    display: none;
}
@media (max-width: 600px) {
header .nav-header-text-mobile {
        display: inherit;
}
}
header .header-cta {
    height: 30px;
    line-height: 30px;
    text-align: center;
    background-color: #af7056;
    color: white;
}
header .nav {
    background-color: #FFFDF5;
    color: #383836;
    text-align: center;
    line-height: 60px;
    height: 60px;
    border: 2px solid #f0f3f5;
}
header .nav .header-right {
      position: absolute;
      right: 20px;
}
header .nav .header-right .underlined {
        border-bottom: 2px solid #702626;
}
header .nav .hamburger {
      width: 45px;
      height: 22px;
      cursor: pointer;
      position: absolute;
      left: 20px;
      margin-top: 18px;
}
@media (max-width: 600px) {
header .nav .hamburger {
          width: 35px;
          height: 18px;
          margin-top: 20px;
}
}
header .nav .bar,
    header .nav .bar:after,
    header .nav .bar:before {
      width: 45px;
      height: 2px;
}
@media (max-width: 600px) {
header .nav .bar,
        header .nav .bar:after,
        header .nav .bar:before {
          width: 35px;
          height: 2px;
}
}
header .nav .bar {
      position: relative;
      transform: translateY(10px);
      transition: all 0ms 300ms;
}
@media (max-width: 600px) {
header .nav .bar {
          transform: translateY(8px);
}
}
header .nav .bar.animate {
        background: rgba(255, 255, 255, 0);
}
header .nav .bar:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 10px;
      background: #af7056;
      transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
@media (max-width: 600px) {
header .nav .bar:before {
          bottom: 8px;
}
}
header .nav .bar:after {
      content: "";
      position: absolute;
      left: 0;
      top: 10px;
      background: #af7056;
      transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
@media (max-width: 600px) {
header .nav .bar:after {
          top: 8px;
}
}
header .nav .bar.animate:after {
      top: 0;
      transform: rotate(45deg);
      transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
header .nav .bar.animate:before {
      bottom: 0;
      transform: rotate(-45deg);
      transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
}
header .evolution-wallpaper {
    font-family: "Playfair Display", serif;
    font-weight: 700;
    font-size: 30px;
    color: #702626;
}
header a {
    color: #383836;
    text-decoration: none;
    padding: 5px;
}
header .fade-enter-active, header .fade-leave-active {
    transition: opacity 1s;
}
header .fade-enter, header .fade-leave-to {
    opacity: 0;
}
header .header-heart {
    color: red;
    font-size: 2em;
}
@media (max-width: 800px) {
header .header-heart {
        font-size: 1.75em;
}
}
@media (max-width: 600px) {
header .header-heart {
        font-size: 1.5em;
}
}
header .favorites-count {
    position: absolute;
    background-color: white;
    color: #383836;
    height: 20px;
    width: 20px;
    line-height: 20px;
    text-align: center;
    border: 1px solid red;
    left: 22px;
    top: 4px;
    border-radius: 25px;
    font-size: .8em;
}
@media (max-width: 800px) {
header .favorites-count {
        font-size: .75em;
        left: 20px;
        top: 6px;
}
}
@media (max-width: 600px) {
header .favorites-count {
        font-size: .65em;
        width: 18px;
        height: 18px;
        top: 8px;
        left: 18px;
}
}
header .fullsize-menu {
    width: 100%;
    height: calc(100vh - 94px);
    position: absolute;
    background-color: #FFFDF5;
    display: flex;
    align-content: center;
    justify-content: center;
    transition: all 0.5s ease;
    border: 2px solid #f0f3f5;
    box-sizing: border-box;
    font-family: "Playfair Display", serif;
    text-transform: uppercase;
    text-align: center;
}
header .fullsize-menu ul {
      list-style-type: none;
      padding: 20vh 0;
      display: flex;
      align-content: space-between;
      flex-direction: column;
}
@media (max-width: 600px) {
header .fullsize-menu ul {
          padding: 8vh 0;
}
}
header .fullsize-menu ul li {
        font-size: 2em;
        flex-grow: 1;
}
@media (max-width: 600px) {
header .fullsize-menu ul li {
            font-size: 1.75em;
}
}
header .fullsize-menu ul li a:hover {
          color: #af7056;
          -webkit-transition: color .5s;
          transition: color .5s;
}
footer {
  width: 100%;
  z-index: 99;
  background-color: #af7056;
  color: white;
  border: 15px solid #eee5e9;
  box-sizing: border-box;
  text-align: center;
}
@media (max-width: 600px) {
footer {
      border: none;
}
}
footer .social-media-footer-icon {
    width: 75px;
}

/*
 END GLOBAL STYLES
  */
/*
START HOME
*/
#home-container {
  margin: 5px 15px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  justify-content: space-between;
}
@media (max-width: 1200px) {
#home-container {
      flex-direction: column;
}
}
#home-container #home-main-content {
    border: 5px double #af7056;
    width: 59%;
    height: calc(100vh - (105px + 40px));
    box-sizing: border-box;
    background-image: url("https://assets.fathead.com/evolution/homepage.jpg");
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
#home-container #home-main-content #home-main-cta {
      border: 5px double #af7056;
      background-color: #FFFDF5;
      text-align: center;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 25px;
      max-width: 80%;
}
#home-container #home-main-content img {
      width: 100%;
}
@media (max-width: 1200px) {
#home-container #home-main-content {
        width: 100%;
        margin-bottom: 10px;
}
}
#home-container #home-side-content {
    width: 40%;
    height: calc(100vh - (105px + 40px));
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    justify-content: space-between;
}
@media (max-width: 1200px) {
#home-container #home-side-content {
        width: 100%;
}
}
#home-container #home-side-content .side-item {
      height: 49.5%;
      border: 5px double #af7056;
      width: 100%;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-color: #FFFDF5;
      background-size: cover;
      background-position: center;
}
@media (max-width: 1200px) {
#home-container #home-side-content .side-item {
          height: 50%;
          margin-bottom: 10px;
}
}

/*
END HOME
*/
/*
 START WALLPAPER PATTERN
 */
.wallpaper-pattern {
  word-wrap: break-word;
  border: 1px solid #f0f3f5;
  background-color: white;
  color: #383836;
  display: flex;
  justify-content: center;
  position: relative;
}
.wallpaper-pattern img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.wallpaper-pattern a {
    text-decoration: none;
    color: #702626;
}
.wallpaper-pattern h1, .wallpaper-pattern h2, .wallpaper-pattern h3, .wallpaper-pattern h4, .wallpaper-pattern h5 {
    padding: 0;
    margin: 0;
    text-align: center;
    background-color: #FFFDF5;
}

/*
 END WALLPAPER PATTERN
 */
/*
 START FILTER SEARCH PAGE
 */
.patterns {
  display: grid;
  grid-gap: 45px 10px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 200px;
}
.filter-search .filter-container {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  position: relative;
  margin: 5px 15px;
}
.filter-search .filters {
  width: 20%;
  min-width: 300px;
  background-color: #FFFDF5;
  color: #383836;
  border: 2px solid #f0f3f5;
  box-sizing: border-box;
  padding: 0 1%;
  margin-right: 15px;
}
@media (max-width: 800px) {
.filter-search .filters {
      display: none;
}
}
.filter-search .filters-mobile {
  display: none;
  position: fixed;
  width: 70%;
  height: 100%;
  box-sizing: border-box;
  padding: 2% 5%;
  z-index: 99;
  background-color: #FFFDF5;
  overflow: scroll;
  right: 0;
  top: 0;
}
@media (max-width: 800px) {
.filter-search .filters-mobile {
      display: block;
}
}
.filter-search #mobile-filters-button {
  float: right;
  margin: 5px 0;
}
@media (min-width: 800px) {
.filter-search #mobile-filters-button {
      display: none;
}
}
.filter-search .overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 99;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.filter-search .results {
  width: 80%;
  background-color: #FFFDF5;
  color: #383836;
  border: 2px solid #f0f3f5;
  padding: 0 1%;
  box-sizing: border-box;
}
@media (max-width: 800px) {
.filter-search .results {
      width: 100%;
}
}
.activeFilter {
  outline: 1.5px solid #702626;
  outline-offset: 2px;
}
.activePatternFilter:before {
  content: 'X ';
  color: #702626;
}
.activeFilterText {
  font-weight: 800;
}
.color-filters {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.pattern-filter-option {
  cursor: pointer;
}
.color-swatch {
  box-sizing: border-box;
  height: 25px;
  width: 25px;
  margin: 7.5px;
  border: 1px solid #f0f3f5;
}

/*
 END FILTER SEARCH
 */
/*
 START FAVORITES
 */
.favorites {
  display: grid;
  grid-gap: 45px 20px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 200px;
  padding-bottom: 30px;
}
.add-to-favorites {
  position: absolute;
  right: 2px;
  top: 0px;
}
.add-to-favorites .heart {
    color: red;
    cursor: pointer;
    font-size: 20px;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
}
.add-to-favorites .added {
    color: black !important;
}

/*
 END FAVORITES
 */
/*
 START ARTISTS PAGE
 */
.artists-page p {
  display: inline-block;
  margin: 0 .5em;
  vertical-align: top;
}
.artists-page img {
  max-width: 200px;
}
.artists-page .artists-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 5px 15px;
}
@media (max-width: 600px) {
.artists-page .artists-container {
      margin: 5px 10px;
}
}
.artists-page .artists-container .artist-content-block {
    border: 2px solid #f0f3f5;
    margin-bottom: 15px;
    padding: 10px 20px;
    background-color: #FFFDF5;
    width: 200px;
}
@media (max-width: 800px) {
.artists-page .artists-container .artist-content-block {
        width: 100%;
        text-align: center;
}
}

/*
 END ARTISTS PAGE
 */
/*
 START ARTIST PAGE
 */
.artist-page img {
  max-width: 200px;
}

/*
 END ARTIST PAGE
 */
/*
 START EDUCATION PAGE
 */
.education-page img {
  max-width: 200px;
}

/*
 END EDUCATION PAGE
 */
/*
 START PDP PAGE
 */
.pdp .content-block {
  text-align: center;
}
.pdp-wrap {
  display: inline-block;
  width: 80%;
}
@media (max-width: 1200px) {
.pdp-wrap {
      width: 100%;
}
}
.pdp-wrap .pdp-inner {
    text-align: center;
    display: flex;
    align-content: center;
    flex-direction: row;
}
@media (max-width: 800px) {
.pdp-wrap .pdp-inner {
        flex-direction: column;
}
}
.pdp-wrap .pdp-inner .image-wrap {
      width: 55%;
}
@media (max-width: 800px) {
.pdp-wrap .pdp-inner .image-wrap {
          width: 100%;
}
}
.pdp-wrap .pdp-inner .product-detail-wrap {
      width: 45%;
      text-align: left;
}
@media (max-width: 800px) {
.pdp-wrap .pdp-inner .product-detail-wrap {
          width: 80%;
          align-self: center;
}
}
.pdp-wrap .pdp-inner .product-detail-wrap .add-to-cart .button {
        width: 100%;
        text-align: center;
        padding-left: 0;
        padding-right: 0;
        max-width: 300px;
}
@media (max-width: 800px) {
.pdp-wrap .pdp-inner .product-detail-wrap .add-to-cart .button {
            max-width: 1000px;
}
}
.pdp-wrap .pdp-inner .product-detail-wrap .price {
        font-size: 1.75em;
        margin: 30px 0;
}
.pdp-wrap .pdp-inner .product-detail-wrap .material-picker {
        display: flex;
        justify-content: space-between;
        width: 100%;
        max-width: 300px;
}
@media (max-width: 800px) {
.pdp-wrap .pdp-inner .product-detail-wrap .material-picker {
            max-width: 1000px;
}
}
.pdp-wrap .pdp-inner .product-detail-wrap .material-picker .material-option {
          width: 100px;
          padding: 10px 20px;
          text-align: center;
          cursor: pointer;
          line-height: 1.25em;
}
.pdp-wrap .pdp-inner .product-detail-wrap .material-picker .material-option.selected {
            border-color: #702626;
            background-color: #702626;
            color: #fff;
}
@media (max-width: 800px) {
.pdp-wrap .pdp-inner .product-detail-wrap .material-picker .material-option {
              width: 48%;
              padding: 10px 0;
}
}
.pdp-wrap .pdp-inner .pdp-image {
      width: 80%;
}

/*
 END PDP PAGE
 */
/*
 START CART
 */
.cart-page .cart-container {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  position: relative;
}
@media (max-width: 800px) {
.cart-page .cart-container {
      flex-direction: column-reverse;
}
}
.cart-list {
  width: 70%;
  box-sizing: border-box;
  display: inline-block;
}
@media (max-width: 1200px) {
.cart-list {
      width: 60%;
}
}
@media (max-width: 800px) {
.cart-list {
      width: 100%;
}
}
.cart-list .cart-list-inner {
    width: 90%;
    margin: auto;
}
.cart-total {
  width: 30%;
  box-sizing: border-box;
  display: inline-block;
  margin-left: 15px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1200px) {
.cart-total {
      width: 40%;
}
}
@media (max-width: 800px) {
.cart-total {
      width: 100%;
      margin-left: 0;
}
}
.cart-total .cart-line {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
}
.cart-item {
  display: grid;
  grid-template-columns: 150px 1fr 20px;
  grid-template-rows: auto 1fr auto;
  margin-bottom: 50px;
  grid-column-gap: 20px;
}
@media (max-width: 1200px) {
.cart-item {
      grid-template-rows: auto 2fr 1fr auto;
}
}
@media (max-width: 600px) {
.cart-item {
      grid-template-rows: auto auto auto auto auto;
      grid-template-columns: 1fr;
      grid-row-gap: 5px;
}
}
.cart-item .cart-image-wrap {
    grid-area: 1 / 1 / -1 / 2;
}
@media (max-width: 600px) {
.cart-item .cart-image-wrap {
        grid-area: 1 / 1 / 2 / -1;
}
}
.cart-item .cart-image-wrap .cart-image {
      width: 150px;
      height: 150px;
      display: inline-block;
}
@media (max-width: 600px) {
.cart-item .cart-image-wrap .cart-image {
          width: 100%;
          height: auto;
}
}
.cart-item .pattern-name {
    grid-area: 1 / 2 / 2 / 3;
}
@media (max-width: 600px) {
.cart-item .pattern-name {
        grid-area: 2 / 1 / 3 / -1;
}
}
.cart-item .subtext {
    grid-area: 2 / 2 / 3 / 3;
}
@media (max-width: 600px) {
.cart-item .subtext {
        display: none;
}
}
.cart-item .button-wrap {
    grid-area: 3 / 2 / -1 / 3;
}
@media (max-width: 1200px) {
.cart-item .button-wrap {
        grid-area: 4 / 2 / 5 / 3;
}
}
@media (max-width: 600px) {
.cart-item .button-wrap {
        grid-area: 5 / 1 / 6 / -1;
}
}
.cart-item .cart-item-price {
    grid-area: 1 / 3 / 2 / 4;
}
@media (max-width: 1200px) {
.cart-item .cart-item-price {
        grid-area: 3 / 2 / 4 / 3;
}
}
@media (max-width: 600px) {
.cart-item .cart-item-price {
        grid-area: 4 / 1 / 5 / -1;
}
}

